<template><section><h1>Table 表格</h1>
<hr>
<h3>基础用法</h3>
<p>传入<code v-pre="" class="code_inline">columns</code>和<code v-pre="" class="code_inline">data</code>来新建表格。</p>
<h4>代码示例：</h4>
<div class="demo-block">
    <gf-table :columns="columns1" :data="data1"></gf-table>
</div>
<demo-block>
                        <div slot="desc"><template>
    <gf-table :columns="columns1" :data="data1"></gf-table>
</template>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">gf-table</span> <span class="hljs-attr">:columns</span>=<span class="hljs-string">"columns1"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data1"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">gf-table</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
    <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
        data() {
            <span class="hljs-keyword">return</span> {
                <span class="hljs-attr">columns1</span>: [
                    {
                        <span class="hljs-attr">title</span>: <span class="hljs-string">'Name'</span>,
                        <span class="hljs-attr">key</span>: <span class="hljs-string">'name'</span>
                    },
                    {
                        <span class="hljs-attr">title</span>: <span class="hljs-string">'Age'</span>,
                        <span class="hljs-attr">key</span>: <span class="hljs-string">'age'</span>
                    },
                    {
                        <span class="hljs-attr">title</span>: <span class="hljs-string">'Address'</span>,
                        <span class="hljs-attr">key</span>: <span class="hljs-string">'address'</span>
                    }
                ],
                <span class="hljs-attr">data1</span>: [
                    {
                        <span class="hljs-attr">name</span>: <span class="hljs-string">'John Brown'</span>,
                        <span class="hljs-attr">age</span>: <span class="hljs-number">18</span>,
                        <span class="hljs-attr">address</span>: <span class="hljs-string">'New York No. 1 Lake Park'</span>,
