                    },
                    {
                        <span class="hljs-attr">name</span>: <span class="hljs-string">'Jim Green'</span>,
                        <span class="hljs-attr">age</span>: <span class="hljs-number">24</span>,
                        <span class="hljs-attr">address</span>: <span class="hljs-string">'London No. 1 Lake Park'</span>,
                        <span class="hljs-attr">date</span>: <span class="hljs-string">'2016-10-01'</span>
                    },
                    {
                        <span class="hljs-attr">name</span>: <span class="hljs-string">'Joe Black'</span>,
                        <span class="hljs-attr">age</span>: <span class="hljs-number">30</span>,
                        <span class="hljs-attr">address</span>: <span class="hljs-string">'Sydney No. 1 Lake Park'</span>,
                        <span class="hljs-attr">date</span>: <span class="hljs-string">'2016-10-02'</span>
                    },
                    {
                        <span class="hljs-attr">name</span>: <span class="hljs-string">'Jon Snow'</span>,
                        <span class="hljs-attr">age</span>: <span class="hljs-number">26</span>,
                        <span class="hljs-attr">address</span>: <span class="hljs-string">'Ottawa No. 2 Lake Park'</span>,
                        <span class="hljs-attr">date</span>: <span class="hljs-string">'2016-10-04'</span>
                    }
                ]
            };
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<hr>
<h3>隔行变色和边框</h3>
<p>传入<code v-pre="" class="code_inline">stripe</code>控制隔行变色，传入<code v-pre="" class="code_inline">border</code>控制表格边框</p>
<h4>代码示例:</h4>
<div class="demo-block">
    <gf-table :columns="columns1" :data="data1" stripe="" border=""></gf-table>
</div>
<demo-block>
                        <div slot="desc"><template>
    <gf-table :columns="columns1" :data="data1" stripe="" border=""></gf-table> 
</template>
</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">gf-table</span> <span class="hljs-attr">:columns</span>=<span class="hljs-string">"columns1"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data1"</span> <span class="hljs-attr">stripe</span> <span class="hljs-attr">border</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">gf-table</span>&gt;</span> 
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
</code></pre>
</div></demo-block>
<hr>
<h3>在表格中设置不同的样式</h3>
<h4>自定义行样式：</h4>
<p>表格接受一个函数<code v-pre="" class="code_inline">row-class-name</code>，参数为 <strong>当前行数据</strong> 和 <strong>当前行索引</strong> ，返回结果为 <strong>样式名称</strong></p>
<div class="demo-block">
    <gf-table :row-class-name="rowClassName" :columns="columns1" :data="data1"></gf-table>
</div>
<h4>自定义列样式：</h4>
<p>要定义自定义列样式，要在列columns对象的某列里加上<code v-pre="" class="code_inline">className</code>字段，属性值即为 <strong>样式名称</strong></p>
<div class="demo-block">
    <gf-table :columns="columns9" :data="data1"></gf-table>
</div>    
<h4>自定义单元格样式：</h4>
<p>通过给数据 data 设置字段 <code v-pre="" class="code_inline">cellClassName</code> 可以给任意一个单元格指定样式</p>
<div class="demo-block">
    <gf-table :columns="columns1" :data="data8"></gf-table>
</div>  
<h4>代码示例：</h4>
<demo-block>
