<h3>固定列</h3>
<p>通过给数据 <code v-pre="" class="code_inline">columns</code> 的项设置 <code v-pre="" class="code_inline">fixed</code> 为 <code v-pre="" class="code_inline">left</code> 或 <code v-pre="" class="code_inline">right</code>，可以左右固定需要的列。当横向内容过多时可以使用。</p>
<h4>代码示例：</h4>
<div class="demo-block">
    <gf-table width="550" border="" :columns="columns2" :data="data3"></gf-table>
</div>
<demo-block>
                        <div slot="desc">
<template>
    <gf-table width="550" border="" :columns="columns2" :data="data3"></gf-table>
</template>

</div>
                        <div slot="highlight"><pre v-pre=""><code v-pre="" class="language-html">
<span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">gf-table</span> <span class="hljs-attr">width</span>=<span class="hljs-string">"550"</span> <span class="hljs-attr">border</span> <span class="hljs-attr">:columns</span>=<span class="hljs-string">"columns2"</span> <span class="hljs-attr">:data</span>=<span class="hljs-string">"data3"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">gf-table</span>&gt;</span>
