                        <span class="hljs-attr">key</span>: <span class="hljs-string">'name'</span>,
                        <span class="hljs-attr">width</span>: <span class="hljs-number">100</span>,
                        <span class="hljs-attr">fixed</span>: <span class="hljs-string">'left'</span>
