<h3>行内输入框编辑</h3>
<p>通过给 <code v-pre="" class="code_inline">columns</code> 数据的项，设置 <code v-pre="" class="code_inline">type: input</code>，同时设置一个回调函数 <code v-pre="" class="code_inline">onChangeData</code> ,点击该列的所有单元格即可开启编辑</p>
<p>回调函数 <code v-pre="" class="code_inline">onChangeData</code> 接受两个参数：</p>
<ul>
<li><code v-pre="" class="code_inline">row</code> ：当前行数据</li>
<li><code v-pre="" class="code_inline">column</code> ： 当前列数据的key</li>
</ul>
<p>该回调函数只有在返回 <code v-pre="" class="code_inline">true</code> 时才会改变表格当前数据，返回 <code v-pre="" class="code_inline">false</code> 则不会改变数据。如果需要更加复杂的功能，可以使用 <code v-pre="" class="code_inline">render</code> 选项。</p>
<p><em>注意</em>，修改单元格数据并不会改变源数据 <code v-pre="" class="code_inline">data</code>，但是通过API获取数据会获取到改变后的 <code v-pre="" class="code_inline">data</code></p>
<h4>代码示例：</h4>
