<hr>
<h3>远程分页</h3>
<p>当需要远程分页时，需要给 <code v-pre="" class="code_inline">page</code> 项设置字段 <code v-pre="" class="code_inline">isServerPage</code> 为 <code v-pre="" class="code_inline">true</code>，此时 <code v-pre="" class="code_inline">page</code> 接受三个必要字段</p>
<ul>
<li><code v-pre="" class="code_inline">pageSize</code>: 单页数量大小</li>
<li><code v-pre="" class="code_inline">totalPage</code>: 总页数</li>
<li><code v-pre="" class="code_inline">pagingCB</code>: 点击的回调函数，参数为 <strong>上一页(int)</strong> 、<strong>下一页(int)</strong> 、<strong>pageSize(int)</strong> 、 <strong>当前排序规则(Object)</strong></li>
</ul>
<p><em>注意</em>：远程分页时，设置的 <code v-pre="" class="code_inline">pageSize</code> 必须要大于等于 <code v-pre="" class="code_inline">data</code> 的长度</p>
<h4>代码示例：</h4>
<div class="demo-block">
    <gf-table :page="page2" :columns="columns1" :data="data5" border=""></gf-table>
</div>
