<hr>
<h3>多选</h3>
<p>对列<code v-pre="" class="code_inline">columns</code> 对象设置一个单独的对象，指定<code v-pre="" class="code_inline">type: selection</code>，则该列为多选列，表格开启多选功能</p>
<p>给 data 项设置特殊 key <code v-pre="" class="code_inline">_checked: true</code> 可以默认选中当前项。</p>
<p>给 data 项设置特殊 key <code v-pre="" class="code_inline">_disabled: true</code> 可以禁止选择当前项。</p>
<p>正确使用好以下事件，可以达到需要的效果：</p>
<ul>
<li><code v-pre="" class="code_inline">@on-select</code>，选中某一项触发，返回值为 <strong>selection</strong> 和 <strong>row</strong>，分别为已选项和刚选择的项。</li>
<li><code v-pre="" class="code_inline">@on-select-all</code>，点击全选时触发，返回值为 <strong>selection</strong>，<strong>已选项</strong>。</li>
<li><code v-pre="" class="code_inline">@on-selection-change</code>，只要选中项发生变化时就会触发，返回值为 <strong>selection</strong>，<strong>已选项</strong>。</li>
</ul>
<h4>示例代码：</h4>
<div class="demo-block">
    <gf-table ref="table" :columns="columns13" :data="data12" border="" style="margin-bottom:10px"></gf-table>
    <button @click="selectTableAll(true)">选中全部</button>
    <button @click="singleSelect(3)">选中第三行</button>
    <button @click="selectTableAll(false)">全部取消选中</button>
</div>
<demo-block>
