<h3>自定义列模板</h3>
<p>通过给 <code v-pre="" class="code_inline">columns</code> 数据的项，设置一个函数 <code v-pre="" class="code_inline">render</code>，可以自定义渲染当前列，包括渲染自定义组件，它基于 <code v-pre="" class="code_inline">Vue</code> 的 <code v-pre="" class="code_inline">Render</code> 函数。</p>
<p><code v-pre="" class="code_inline">render</code> 函数传入两个参数，第一个是 <code v-pre="" class="code_inline">h</code>，第二个是对象，包含 <code v-pre="" class="code_inline">row、column 和 index</code>，分别指当前单元格数据，当前列数据，当前是第几行。</p>
<p><a href="https://cn.vuejs.org/v2/guide/render-function.html">学习 Render函数</a></p>
