                alert(<span class="hljs-string">`content: Name：<span class="hljs-subst">${<span class="hljs-keyword">this</span>.data6[index].name}</span>&lt;br&gt;Age：<span class="hljs-subst">${<span class="hljs-keyword">this</span>.data6[index].age}</span>&lt;br&gt;Address：<span class="hljs-subst">${<span class="hljs-keyword">this</span>.data6[index].address}</span>`</span>)
            },
            remove (index) {
                <span class="hljs-keyword">this</span>.data6.splice(index, <span class="hljs-number">1</span>);
            }
        }
    }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>

