<hr>
<h3>可展开</h3>
<p>当表格内容较多不能一次性完全展示时使用。</p>
<p>通过给 <code v-pre="" class="code_inline">columns</code> 数据设置一项，指定 <code v-pre="" class="code_inline">type: 'expand'</code>，即可开启扩展功能。</p>
<p>给行数据 <code v-pre="" class="code_inline">data</code> 的某项设置 <code v-pre="" class="code_inline">_expanded 为 true</code>，可以默认展开当前行，设置 <code v-pre="" class="code_inline">_disableExpand</code> 可以禁用当前行的展开功能。</p>
<p>渲染展开区域与自定义列模板方法类似，使用 <code v-pre="" class="code_inline">render</code> 函数。当内容较复杂时，可拆分为组件或使用 <code v-pre="" class="code_inline">JSX</code>。</p>
<p><a href="https://cn.vuejs.org/v2/guide/render-function.html">学习 Render函数</a></p>
<h4>代码示例：</h4>
