<h3>API</h3>
<h4>Table props</h4>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>data</td>
<td>显示的结构化数据</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>columns</td>
<td>表格列的配置描述，具体详见后文</td>
<td>Array</td>
<td>[ ]</td>
</tr>
<tr>
<td>stripe</td>
<td>是否显示间隔斑马纹</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>border</td>
<td>是否显示纵向边框</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>height</td>
<td>表格高度，单位 px，设置后会固定表头</td>
<td>Number or String</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>表格宽度，单位 px</td>
<td>Number or String</td>
<td>自动</td>
</tr>
<tr>
<td>page</td>
<td>分页设置， 具体详见后文</td>
<td>Object</td>
<td>{ }</td>
</tr>
<tr>
<td>disabled-hover</td>
<td>禁止表格Hover状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>rowClassName</td>
<td>设置行样式的回调函数，传入参数: row：当前行数据 index：当前行的索引， 可返回当前行的样式</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>loading</td>
<td>设置表格是否在加载中状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>draggable</td>
<td>设置表格是否可拖动</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<br>
<h4>Table events</h4>
<table class="table"><thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-select</td>
<td>在多选模式下有效，选中某一项时触发</td>
<td>selection：已选项数据； row：刚选择的项数据</td>
</tr>
<tr>
<td>on-select-cancel</td>
<td>在多选模式下有效，取消选中某一项时触发</td>
<td>selection：已选项数据；row：取消选择的项数据</td>
</tr>
<tr>
<td>on-select-all</td>
<td>在多选模式下有效，点击全选时触发</td>
<td>selection：已选项数据</td>
</tr>
<tr>
<td>on-selection-change</td>
<td>在多选模式下有效，只要选中项发生变化时就会触发</td>
<td>selection：已选项数据</td>
</tr>
<tr>
<td>on-sort-change</td>
<td>排序时有效，当点击排序时触发</td>
<td>column：当前列数据；key：排序依据的指标；order：排序的顺序，值为 asc 或 desc；currentPage：有分页时当前所在页；pageSize：有分页时，一页显示数量</td>
</tr>
<tr>
<td>on-row-click</td>
<td>单击某一行时触发</td>
<td>row:当前行的数据；index：当前行索引</td>
</tr>
<tr>
<td>on-row-dblclick</td>
<td>双击某一行时触发</td>
<td>row:当前行的数据；index：当前行索引</td>
</tr>
<tr>
<td>on-expand</td>
<td>展开或收起某一行时触发</td>
<td>row：当前行的数据；status：当前的状态</td>
</tr>
<tr>
<td>on-drag-end</td>
<td>拖拽一行结束后触发</td>
<td>newIndex：拖拽行前的索引；oldIndex：拖拽行后的索引，item：当前拖拽的元素(dom元素)</td>
</tr>
<tr>
<td>on-row-mousein</td>
<td>鼠标进入某一行时触发</td>
<td>row:当前行的数据；index：当前行索引</td>
</tr>
<tr>
<td>on-row-mouseout</td>
<td>鼠标离开某一行时触发</td>
<td>row:当前行的数据；index：当前行索引</td>
</tr>
</tbody>
</table>
<br>
<h4>Table slot</h4>
<table class="table"><thead>
<tr>
<th>名称</th>
<th>说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>nodata</td>
<td>暂无数据时的显示</td>
</tr>
</tbody>
</table>
<br>
<h4>Table methods</h4>
<table class="table"><thead>
<tr>
<th>方法名</th>
<th>说明</th>
<th>参数</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>getSelection</td>
<td>多选模式下、获取当前选中的数据</td>
<td>-</td>
<td>选中数据(Array)</td>
</tr>
<tr>
<td>selectAll</td>
<td>多选模式下、全选或者全不选</td>
<td>Boolean：传入True则全选</td>
<td>-</td>
</tr>
<tr>
<td>toggleSelect</td>
<td>多选模式下，单独选中一个</td>
<td>index：选中数据的索引</td>
<td>-</td>
</tr>
<tr>
<td>getTableData</td>
<td>获取当前表格数据(排序过后)</td>
<td>-</td>
<td>当前表格数据(Array)</td>
</tr>
</tbody>
</table>
<br>
<h4>column</h4>
<p>列描述数据对象，是 columns 中的一项</p>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td>列类型，可选值为 index、selection、expand、input</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>title</td>
<td>列头显示文字</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>key</td>
<td>对应列内容的字段名</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>width</td>
<td>该列宽度（注意：如果每列都设置宽度，那设置的总宽度不能小于外部容器宽度）</td>
<td>Number</td>
<td>自动</td>
</tr>
<tr>
<td>align</td>
<td>对齐方式，可选值为 left 左对齐、right 右对齐和 center 居中对齐</td>
<td>String</td>
<td>left</td>
</tr>
<tr>
<td>className</td>
<td>列的样式名称</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>fixed</td>
<td>列是否固定在左侧或者右侧，可选值为 left 左侧和 right 右侧</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>render</td>
<td>自定义渲染列，使用 Vue 的 Render 函数。传入两个参数，第一个是 h，第二个为对象，包含 row、column 和 index，分别指当前行数据，当前列数据，当前行索引，详见示例。</td>
<td>Funtion</td>
<td>-</td>
</tr>
<tr>
<td>renderHeader</td>
<td>自定义列头显示内容，使用 Vue 的 Render 函数。传入两个参数，第一个是 h，第二个为对象，包含 column 和 index，分别为当前列数据和当前列索引。</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>sortable</td>
<td>对应列是否可以排序，如果设置为 custom，则代表用户希望远程排序，需要监听 Table 的 on-sort-change 事件</td>
<td>Boolean</td>
<td>false</td>
</tr>
