<td>String</td>
<td>-</td>
</tr>
<tr>
<td>children</td>
<td>表头分组,详见示例</td>
<td>Array</td>
<td>-</td>
</tr>
<tr>
<td>onChangeData</td>
<td>开启单元格编辑后，数据改变后的回调函数，详见示例</td>
<td>Funtion</td>
<td>-</td>
</tr>
</tbody>
</table>
